<template>
  <div>
    <v-card elevation="10" class="rounded-lg">
      <v-card-title>
        视频列表
        <v-spacer></v-spacer>
        <v-icon large class="mr-2"></v-icon>
        <v-icon large class="mr-2">
          {{
            displayMethod == "list"
              ? "mdi-format-list-bulleted-square"
              : "mdi-apps"
          }}
        </v-icon>
        <v-icon large @click="getVideoList()">mdi-cached</v-icon>
      </v-card-title>
      <!-- 分割线 -->
      <v-divider></v-divider>
      <v-card-text>
        <v-text-field
          v-model="search"
          label="搜索"
          single-line
          hide-details
          clearable
        >
          <template v-slot:append-outer>
            <v-icon>mdi-magnify</v-icon>
          </template>
        </v-text-field>
      </v-card-text>
      <v-data-table
        :loading="loading"
        :headers="headers"
        :items="videoList"
        :search="search"
      >
        <template v-slot:item.name="{ item }">
          <v-btn text @click="tiaozhuan(item.name)">{{ item.name }}</v-btn>
        </template>
        <template v-slot:item.size="{ item }">
          {{ $common.bytesToSize(item.size) }}
        </template>
        <template v-slot:item.duration="{ item }">
          {{ $common.formatSecond(item.duration) }}
        </template>
        <template v-slot:item.like="{ item }">
          <v-icon
            :color="item.like ? 'pink' : ''"
            @click="updateVideoLike(item.id, !item.like)"
          >
            {{ item.like ? "mdi-heart" : "mdi-heart-outline" }}
          </v-icon>
        </template>
        <template v-slot:item.operate="{ item }">
          <v-icon class="mr-2" @click="playVideo(item.id)">
            mdi-play-circle-outline
          </v-icon>
          <v-icon
            @click="
              videoId = item.id;
              delVideoDialog = true;
            "
          >
            mdi-delete-circle-outline
          </v-icon>
        </template>
      </v-data-table>
      <!-- <v-data-iterator :items="videoList" hide-default-footer>
