            </v-col>
          </v-row>
        </template>
      </v-data-iterator> -->
    </v-card>
    <v-dialog v-model="delVideoDialog" max-width="250">
      <v-card class="mx-auto">
        <v-card-title>确定删除视频?</v-card-title>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="delVideoDialog = false">取消</v-btn>
          <v-btn color="error" @click="delVideo()">确定</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "VideoList",
  data: () => ({
    displayMethod: "list",
    videoId: null,
    delVideoDialog: false,
    path: "",
    loading: false,
    search: "",
    headers: [
      {
        text: "编号",
        align: "start",
        sortable: false,
        value: "id",
      },
      { text: "名称", value: "name" },
      { text: "是否喜欢", value: "like" },
      // { text: "MD5", sortable: false, value: "md5" },
      { text: "格式", value: "format" },
      { text: "大小", value: "size" },
      { text: "时长", value: "duration" },
      { text: "分辨率", value: "resolution" },
      { text: "创建时间", value: "createTime" },
      { text: "更新时间", value: "updateTime" },
      { text: "操作", value: "operate" },
    ],
    videoList: [],
  }),
  created() {
    this.getVideoList();
  },
  methods: {
    tiaozhuan(name) {
      window.open("http://192.168.1.40:8090/data/" + name);
    },
    getVideoList() {
      this.loading = true;
      this.videoList = [];
      this.$get("/video/list").then((res) => {
        this.videoList = res;
        this.loading = false;
      });
    },
    updateVideoLike(id, isLike) {
      this.$post("/video/update-like", { id: id, like: isLike }).then((res) => {
        if (res) {
          this.getVideoList();
        }
      });
    },
    delVideo() {
      this.$del("/video/" + this.videoId).then((res) => {
        if (res) {
          this.getVideoList();
          this.delVideoDialog = false;
        }
      });
    },
    playVideo(id) {
      let routeUrl = this.$router.resolve({
        path: `/video/${id}`,
      });
      window.open(routeUrl.href, "_blank");
    },
  },
};
</script>
